h1 {
  color: green;
  font-style: italic;
}
.note {
  color: black;
  padding-top: 3px;
  font-size: 15px;
}

.msg {
  color: green;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.alignRight {
  text-align: right;
}
.alignRightBold {
  text-align: right;
  font-weight: bold;
}
.alignLeft {
  text-align: left;
}

.alignRightRed {
  color: red;
  text-align: right;
  font-weight: bold;

  background: lightgrey;
  font-size: 16px;
  border-style: solid;
  border-radius: 2px;
  padding: 5px;
  margin-bottom: 5px;

}

.alignRightOrange {
  color: orange;
  text-align: right;
  font-weight: bold;

  background: lightgrey;
  font-size: 16px;
  border-style: solid;
  border-radius: 2px;
  padding: 5px;
  margin-bottom: 5px;

}